@media screen and (min-width: 1200px) {
  .delegateDetail {
    padding: 25px 10px;
    background: #ffffff;
    border-radius: 8px;
    border: 1px solid rgba(0, 0, 0, 0.06);
  }
  .delegateDetail .nameText {
    min-height: 55px;
    background: #f5f5f5;
    border-radius: 4px;
    word-wrap: break-word;
    padding: 17px 17px;
  }
  .delegateDetail .nameTextnum {
    height: 14px;
    font-size: 16px;
    font-weight: 500;
    color: #000000;
    line-height: 14px;
  }
  .delegateDetail .declarationofcandidacy {
    min-height: 87px;
    background: #f5f5f5;
    border-radius: 4px;
    word-wrap: break-word;
    padding: 17px 17px;
  }
  .delegateDetail .textOfImg {
    word-break: break-all;
  }
  .delegateDetail .inputWrapperVote .ant-input {
    min-height: 55px;
    height: 55px;
  }
  .delegateDetail .inputWrapperVote .ant-select-selection--multiple .ant-select-selection__rendered {
    height: 55px;
  }
  .delegateDetail .inputWrapperVote .ant-form-item-label {
    line-height: 14px !important;
  }
  .delegateDetail .ant-table-tbody > tr > td {
    padding: 16px 16px;
  }
  .delegateDetail .TextVote {
    font-size: 12px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.5);
  }
  .delegateDetail .modalTitletovote .leftIcon {
    display: inline-block;
    width: 4px;
    height: 20px;
    background: #000000;
    border-radius: 2px;
  }
  .delegateDetail .modalTitletovote .rightText {
    margin-left: 14.6px;
    display: inline-block;
    width: 120px;
    height: 48px;
    font-size: 24px;
    font-weight: bold;
    color: #000000;
    line-height: 48px;
  }
}
@media screen and (max-width: 750px) {
  .delegateDetail {
    padding: 25px 10px;
