    padding: 16px 16px;
  }
  .delegateDetail .TextVote {
    font-size: 12px;
    font-weight: 500;
    color: rgba(0, 0, 0, 0.5);
  }
  .modalTitletovote .leftIcon {
    display: inline-block;
    width: 4px;
    height: 20px;
    background: #000000;
    border-radius: 2px;
  }
  .modalTitletovote .rightText {
    margin-left: 14.6px;
    display: inline-block;
    width: 120px;
    height: 48px;
    font-size: 24px;
    font-weight: bold;
    color: #000000;
    line-height: 48px;
  }
  .modalFootertovote .modalFootertovoteEext {
    display: inline-block;
    width: 35px;
    height: 16px;
    font-size: 16px;
    font-weight: bold;
    color: rgba(0, 0, 0, 0.25);
    line-height: 16px;
  }
  .modalFootertovote .modalFootertovoteConfirm {
    margin-left: 16px;
    display: inline-block;
    min-width: 125px;
    width: auto;
    height: 48px;
    background: #1976d1;
    padding: 16px 30px;
    border-radius: 4px;
    font-size: 16px;
    font-weight: bold;
    color: #ffffff;
    line-height: 16px;
  }
  .modalFootertovote .modalFootertovoteConfirm[disabled] {
    color: rgba(0, 0, 0, 0.25);
    background-color: #f5f5f5;
    border-color: #d9d9d9;
    text-shadow: none;
    box-shadow: none;
    cursor: not-allowed;
  }
}
